<!--  -->
<template>
  <div id="material">
    <!-- -->
    <div id="header">
      <el-input v-model="input" placeholder="" @change="searchMaterial"></el-input>
      <el-button type="primary" @click="searchMaterial"></el-button>
      <el-button type="primary" @click="addMaterial"></el-button>
      <!--  -->
      <el-upload
        action=""
        id="upload-excel"
        :on-change="handleChange"
        :show-file-list="false"
        accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.ms-excel"
        :auto-upload="false">
        <el-button type="primary"></el-button>
      </el-upload>
      <!--  -->
      <el-button type="primary" @click="clickDownload"></el-button>
    </div>
    <!--  -->
    <div id="wrapper">
      <el-table
      ref="dataTable"
      size="medium"
      :data="tableData"
      :cell-style="{ textAlign:'center' }"
      :row-style="{ height:'10px' }"
      :header-cell-style="{ textAlign: 'center'}">
        <el-table-column type="selection" width="60"></el-table-column>
        <el-table-column prop="code" label="" width="150"></el-table-column>
        <el-table-column prop="material" label="" width="200" show-overflow-tooltip></el-table-column>
        <el-table-column prop="brand" label="" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="specification" label="" width="150" show-overflow-tooltip></el-table-column>
        <el-table-column prop="article" label="" width="120" show-overflow-tooltip></el-table-column>
        <el-table-column prop="unit" label="" width="80"></el-table-column>
        <el-table-column prop="number" label="" width="80"></el-table-column>
        <el-table-column prop="receiving" label="" width="120"></el-table-column>
        <el-table-column prop="" label="" width="180">
          <template slot-scope="scope">
            <el-button size="mini" @click="editMaterial(scope.$index, scope.row)"></el-button>
            <el-button size="mini" type="danger" @click="delMaterial(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--  -->
    <Pagination :total='total' :pageSize='pageSize' @changePage='changePage' :currentPage='currentPage'/>
    <!--  -->
    <AddMaterial ref="dialog" :title="title" :rowData="rowData"/>
  </div>
</template>

<script>
  import Pagination from '@/components/Pagination.vue' //
  import AddMaterial from './components/AddMaterial.vue' //
  import importExcel from '@/assets/js/iexcel'
  import { exportJsonToExcel } from '@/assets/js/oexcel.js'
  import Moment from 'moment'

  export default{
    components:{
      Pagination,
      AddMaterial
    },
    data(){
      return{
        input: "",
        tableData: [],    //
        total: 10,        //
        pageSize: 1,      //
        type: 1,           //
        dialogVisible: false, //
        currentPage: 1,     //
        title: '',  //
        rowData:{}   //
        
      }
    },
    methods: {
      /*  */
      changePage(num){
        this.currentPage=num;
        if(this.type==1){
          this.materialList(num);
        }else{
          this.tableData = this.list.slice((num-1)*3,num*3)
        }
      },
      /*  */
      materialList(page){
        this.$api.getMaterialList({
          page
        }).then(res => {
          if(res.data.status==200){
            this.$date.dateFilter(res.data.data);
            this.tableData=res.data.data;
            this.total = res.data.total;
            this.pageSize = res.data.pageSize;
          }
        })
      },

      /* -- */
      changeDialog(){
        this.dialogVisible = false
      },
      /*  */
      addMaterial(){
        this.$refs.dialog.dialogVisible = true
      },
      /*  */
      delMaterial(index, row) {
        this.$confirm("?","",{
          confirmButtonText: "",
          cancelButton: "",
          type:"warning"
        }).then(()=>{
          //id
          this.$api.delMaterial({
            id:row.id
          }).then(res=>{
              if(res.data.status==200){
                this.materialList(1) //
                this.$message({message:'!',type:'success'})
              }else{
                this.$message.error('!')
              }
            })
        }).catch(()=>{
          this.$message({type: "info", message: "!"})
        })
      },
      /*  */
      searchMaterial(){
        //
        this.currentPage = 1;  //
        var val = this.input;  //
        //
        if(!val){
          this.materialList(1); //
          this.type = 1;    //type=2,type=2type=1
          return
        }
        //,
        this.$api.getSearchMaterial({
          search:val
        }).then(res =>{
          if(res.data.status == 200){
            this.total = res.data.result.length;
            this.tableData = res.data.result.slice(0,3); //
            this.$date.dateFilter(res.data.result);
            this.list = res.data.result;
            this.type = 2;  //2
          }else{
            this.tableData = [];
            this.total = 1;
            this.pageSize = 1;
          }
        })
      },
      /*  */
      editMaterial(index, row) {
        this.$refs.dialog.dialogVisible = true;
        this.title = '';
        this.rowData = row;   //row
      },

      /*  */
      handleChange (file, fileList) {
        const tableField = ['',''],
        tableHeader = {code:'', class:'', material:'',brand:'', specification:'', 
        article:'', unit:'', number:'', batch:'', serial:'', price:'', 
        storage:'', expiration:'', supplier:'', liaison:'', phone:'', 
        receiving:'', remarks:'', register:'', assets:''}
        importExcel(file, tableField,tableHeader).then(k => {
          this.$api.addBatchMaterial(k).then(res=>{
            if(res.data.status==200){
              this.$message({message:res.data.msg,type:'success'});
              setTimeout(()=>{this.materialList(1);},500)//
            }else {
              this.$message.error(res.data.msg)
              return false;
            };
          })
        })
      },
      
      /*  */
      clickDownload () {
        const tableField = ['receiving','code', 'material', 'brand', 'specification', 'article','unit','number',
        'batch','serial','storage', 'expiration', 'supplier', 
        'assets', 'remarks'],
            tableHeader = {receiving:'', code:'',  material:'',brand:'', specification:'', 
        article:'', unit:'', number:'', batch:'', serial:'', 
        storage:'', expiration:'', supplier:'',
        assets:'', remarks:''},
        tableTitle = '',
        templateData = []

        //ids
        let sids=[]
        this.$refs.dataTable.selection.forEach(item=>{
          sids.push(item.id)
        })

        //ids
        if(sids.length>0){
          this.$api.selectMaterial({
            sids:sids,
            tableField:tableField
          }).then(res=>{
          //
          let dataArr = res.data.dataArr
          let obj = {
            header: tableHeader,
            data: dataArr,
            key: tableField,
            title: '',
            filename: ''+Moment(new Date()).format("YYYYMMDD"),
            autoWidth: true,
            sids: sids
          }
            exportJsonToExcel(obj)
            console.log("")
          })
        }else(
          this.$message.warning('!')
        )
       
      }


    },

    /*  */
    created(){
      this.materialList(1)
    }

}



</script>

<style lang="scss" scoped>
  #material{
    margin: 10px;
  }
  #header{
    display: flex;
    button{
      margin-left: 20px;
    }
  }
  #wrapper{
    margin: 20px 0;
  }
</style>



