<template>
  <el-dialog :visible.sync="dialogVisible" width="50%" top="2vh" :close-on-click-modal="false">
    <template slot="title">
        <span>{{title}}</span>
        <el-divider></el-divider>
    </template>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" size="mini">
        <el-row>
          <el-col :span="11">
            <el-form-item label="" prop="code">
              <el-input v-model="ruleForm.code"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="11">
            <el-form-item label="" prop="class">
              <el-select v-model="ruleForm.class" placeholder="" style="width: 100%;">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="11">
            <el-form-item label="" prop="material">
              <el-input v-model="ruleForm.material"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="11">
            <el-form-item label="" prop="assets">
              <el-input v-model="ruleForm.assets"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="7">
            <el-form-item label="" prop="brand">
              <el-input v-model="ruleForm.brand"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item label="" prop="specification">
              <el-input v-model="ruleForm.specification"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="" prop="article">
              <el-input v-model="ruleForm.article"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="7">
            <el-form-item label="" prop="unit">
              <el-select v-model="ruleForm.unit" placeholder="" style="width: 100%;">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item label="" prop="number">
              <el-input v-model="ruleForm.number"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="" prop="price">
              <el-input v-model="ruleForm.price"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="11">
            <el-form-item label="" prop="batch">
              <el-input v-model="ruleForm.batch"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="11">
            <el-form-item label="" prop="serial">
              <el-input v-model="ruleForm.serial"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="11">
            <el-form-item label="" prop="storage">
              <el-select v-model="ruleForm.storage" placeholder="" style="width: 100%;">
                <el-option label="10~30" value="10~30"></el-option>
                <el-option label="2~8" value="2~8"></el-option>
                <el-option label="-15~-25" value="-15~25"></el-option>
                <el-option label="-80" value="-80"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="11">
            <el-form-item label="" prop="expiration">
              <el-date-picker type="date" placeholder="" v-model="ruleForm.expiration" style="width: 100%;"></el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="22">
            <el-form-item label="" prop="supplier">
              <el-input v-model="ruleForm.supplier"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="11">
            <el-form-item label="" prop="liaison">
              <el-input v-model="ruleForm.liaison"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="11">
            <el-form-item label="" prop="phone">
              <el-input v-model="ruleForm.phone"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="11">
            <el-form-item label="" prop="receiving">
              <el-date-picker type="date" placeholder="" v-model="ruleForm.receiving" style="width: 100%;"></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="11">
            <el-form-item label="" prop="register">
              <el-date-picker type="date" placeholder="" v-model="ruleForm.register" style="width: 100%;" disabled></el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="22">
            <el-form-item label="" prop="remarks" style="width: 100%;">
              <el-input type="textarea" v-model="ruleForm.remarks"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
          <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
  </el-dialog>
</template>

<script>
  export default {
    props: {
      title: {
        type: String,
        default: ''
      },
      rowData: {
        type: Object,
        default: function(){
          return {}
        }
      }
    },
    data() {
      return {
        dialogVisible:false,
        /*  */
        ruleForm: {
        code: '',            //
        class: '',           //
        material: '',        //
        brand: '',           //
        specification: '',   //
        article: '',         //
        unit: '',            //
        number: '1',          //
        batch: '',           //
        serial: '',          //
        price: '0.00',           //
        storage: '',         //
        expiration: '2099-12-30',      //
        supplier: '',        //
        liaison: '',         //
        phone: '',           //
        receiving: new Date(),       //
        register: new Date(),//
        assets: '',          //
        remarks: '',         //
        },
        /* */
        rules: {
          code: [
            { required: true, message: '', trigger: 'blur' },
            { min: 8, max: 15, message: ' 8  15 ', trigger: 'blur' }
          ],
          material: [
            { required: true, message: '', trigger: 'blur' },
          ],
          class: [
            { required: true, message: '', trigger: 'change' },
          ],
          brand: [
            { required: true, message: '', trigger: 'blur' },
          ],
          specification: [
            { required: true, message: '', trigger: 'blur' },
          ],
          number: [
            { required: true, message: '', trigger: 'blur' },
          ],
          unit: [
            { required: true, message: '', trigger: 'change' },
          ],
          receiving: [
            { required: true, message: '', trigger: 'change' },
          ],
        },
      };
    },
    methods: {
      /* */
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if(this.title==""){
              console.log("")
              this.$api.editMaterial(this.ruleForm).then(res=>{
                if(res.data.status==200){
                  this.$message({message:'!',type:'success'});
                  setTimeout(()=>{this.resetForm(formName); this.$parent.materialList(1);},1000)//,
                }else {
                  this.$message.error('!')
                }
              })
              return
            }
            console.log("")
            this.$api.addMaterial(this.ruleForm).then(res=>{
              if(res.data.status==200){
                this.$message({message:'!',type:'success'});
                setTimeout(()=>{this.resetForm(formName); this.$parent.materialList(1);},1000)//,
              }else {
                this.$message.error('!')
              }
            })
            return
          }
        })
      },
      /* */
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    },
    /*  */
    watch:{
      rowData(val){
        console.log("");
        this.ruleForm = val;
      }
    }
  };
</script>
<style lang="scss" scoped>
.demo-ruleForm{
  margin-top: -20px;
}
</style>



