/* excel */

/**
 * excel
 *
 * 1.htmltypefileinputel-upload
 * 2.FileReader
 * 3.XLSXXLSX.read()excelworkbookwb
 * 4.XLSX.utils.sheet_to_json()wb Sheetsjson
 * 5.jsonjsonexcel
 * 
 */


/**
 * @param file 
 * @param tableField 
 * tableField: ['userCode', 'userName', 'department', 'major', 'position']
 * excel
 */
 export default function importExcel (file,tableField,tableHeader) {
    return new Promise((resolve, reject) => {
      let f = file.raw // 
      // DOM
      let rABS = false // 
      let reader = new FileReader()
      FileReader.prototype.readAsBinaryString = function (f) {
        let binary = ''
        let rABS = false // 
        let wb // 
        let outdata
        let reader = new FileReader()
        reader.onload = function (e) {
          let bytes = new Uint8Array(reader.result)
          let length = bytes.byteLength
          for (let i = 0; i < length; i++) {
            binary += String.fromCharCode(bytes[i])
          }
          let XLSX = require('xlsx')
          if (rABS) {
            wb = XLSX.read(btoa(binary), { // 
              type: 'base64',
              cellDates:true
            })
          } else {
            wb = XLSX.read(binary, {
              type: 'binary',
              cellDates:true
            })
          }
          outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]) // outdata

          let arr = []
          let keyArr = Object.keys(tableHeader)
          if (keyArr.length > 0) {
            outdata.map(item1=>{
              let obj={}
              keyArr.map(item2 => {
                let h = tableHeader[item2]
                if(typeof(item1[h])=="undefined"){item1[h]='/'}
                obj[item2] = item1[h]
              })
              arr.push(obj)
            })
            console.log(arr)
          }
          // 
          // if (tableField.length > 0) {
          //   let tempArr = Object.keys(outdata[0]) //Object.keys()keys;Object.values()
          //   let tempArrNew = []
          //   for (let i in tempArr) {
          //     for (let k in tableField) {
          //       if (i === k) {
          //         tempArrNew.push({fieldE: tableField[k], fieldC: tempArr[i]})
                  
          //       }
          //     }
          //   }
          //   console.log(Object.keys(tableHeader))
          //   tempArr = tempArrNew
          //   console.log(tempArr)
          //   outdata.map(item => {
          //     let obj = {}
          //     tempArr.map(temp2 => {
          //       //obj[temp2.fieldE] = item[temp2.fieldC]
          //       obj[temp2.fieldE] = item[temp2.fieldE]  //
          //     })
          //     arr.push(obj)
          //   })
          // }else{ //
          //   arr = outdata
          // }

  
          resolve(arr)
        }
        reader.readAsArrayBuffer(f)
      }
  
      if (rABS) {
        reader.readAsArrayBuffer(f)
      } else {
        reader.readAsBinaryString(f)
      }
    })
  }


